<template>
  <el-submenu :index="menu.path || uniqueId">
    <template slot="title">
      <img v-if="menu.img" class="sidemenu-icon" :src="menu.img">
      <i v-else-if="menu.icon" :class="`fa fa-${menu.icon}`" />
      <i v-else-if="menu.iconSvg">
        <d2-icon-svg :name="menu.iconSvg" />
      </i>
      <i v-else class="fa fa-folder-o" />
      <span slot="title">{{ menu.title }}</span>
    </template>
    <template v-for="(child, childIndex) in menu.children">
      <layout-header-aside-menu-item v-if="child.children === undefined" :menu="child" :key="childIndex" />
      <layout-header-aside-menu-sub v-else :menu="child" :key="childIndex" />
    </template>
  </el-submenu>
</template>

<script>
  import {
    uniqueId
  } from 'lodash'
  // 组件
  import layoutMainMenuItem from '../menu-item/menu-item.vue'

  export default {
    name: 'LayoutHeaderAsideMenuSub',
    components: {
      'layout-header-aside-menu-item': layoutMainMenuItem
    },
    props: {
      menu: {
        type: Object,
        required: false,
        default: () => {}
      }
    },
    data() {
      return {
        uniqueId: uniqueId('d2-menu-empty-')
      }
    }
  }
</script>