<template>
  <el-menu-item :index="menu.path || uniqueId" @click="handleClick" :class="isActive">
    <img v-if="menu.img" class="sidemenu-icon" :src="menu.img">
    <i v-else-if="menu.icon" class="sidemenu-icon" :class="`fa fa-${menu.icon}`" />
    <i v-else-if="menu.iconSvg" class="sidemenu-icon">
      <techo-icon-svg :name="menu.iconSvg" />
    </i>
    <!-- <i v-else class="fa fa-file-o" /> -->
    <span slot="title">{{ menu.title || '未命名菜单' }}</span>
  </el-menu-item>
</template>

<script>
  import {
    uniqueId
  } from 'lodash'
  import {
    mapState,
    mapMutations
  } from 'vuex'

  export default {
    name: 'LayoutHeaderAsideMenuItem',
    props: {
      menu: {
        type: Object,
        required: false,
        default: () => {}
      }
    },
    computed: {
      ...mapState('techo/page', [
        'actived'
      ]),
      isActive() {
        return this.actived.meta.title == this.menu.title ? "layout-header-top-menu-active" :
          "layout-header-top-menu-unactive";
      }
    },
    data() {
      return {
        uniqueId: uniqueId('techo-menu-empty-')
      }
    },
    methods: {
      ...mapMutations('techo/menu', [
        'asideActiveSet'
      ]),
      handleClick() {
        this.asideActiveSet(this.menu)
      }
    }
  }
</script>
