<template>
  <div class="layout-header-aside-menu-side">
    <el-menu :collapse="asideCollapse" :unique-opened="true" :default-active="active" ref="menu" @select="handleMenuSelect">
      <div class="layout-header-aside-title">
        <!-- <span class="layout-header-aside-title-text" v-if="!asideCollapse">导航菜单</span> -->
        <span class="layout-header-aside-title-img" :class="[asideCollapse?'layout-header-aside-title-img-right':'layout-header-aside-title-img-left']">
          <img v-if="!asideCollapse" :src="spreadImg" @click="handleSideToggle">
          <img v-else :src="shrinkImg" @click="handleSideToggle">
        </span>
      </div>
      <template v-for="(menu, menuIndex) in aside">
        <layout-header-aside-menu-item class="layout-header-aside-single-menu" v-if="menu.children === undefined" :menu="menu" :key="menuIndex" />
        <layout-header-aside-menu-sub v-else :menu="menu" :key="menuIndex" />
      </template>
    </el-menu>
    <div v-if="aside.length === 0 && !asideCollapse" class="layout-header-aside-menu-empty" flex="dir:top main:center cross:center">
      <techo-icon name="inbox" />
      <span>没有侧栏菜单</span>
    </div>
  </div>
</template>

<script>
  import {
    mapState
  } from 'vuex'
  import menuMixin from '../mixin/menu'
  import layoutMainMenuItem from '../menu-item/menu-item.vue'
  import layoutMainMenuSub from '../menu-sub/menu-sub.vue'
  import BScroll from 'better-scroll'

  export default {
    name: 'LayoutHeaderAsideMenuSide',
    mixins: [
      menuMixin
    ],
    components: {
      'layout-header-aside-menu-item': layoutMainMenuItem,
      'layout-header-aside-menu-sub': layoutMainMenuSub
    },
    data() {
      return {
        active: '',
        asideHeight: 300,
        // 滚动条
        BS: null,
        /** 收缩按钮图标 */
        shrinkImg: require("@/assets/images/main/shrink.png"),
        spreadImg: require("@/assets/images/main/spread.png")
      }
    },
    computed: {
      ...mapState('techo/menu', [
        'aside',
        'asideCollapse'
      ])
    },
    watch: {
      // 折叠和展开菜单的时候销毁 better scroll
      asideCollapse(val) {
        this.scrollDestroy()
        setTimeout(() => {
          this.scrollInit()
        }, 500)
      },
      // 监听路由 控制侧边栏激活状态
      '$route.matched': {
        handler(val) {
          this.active = val[val.length - 1].path
          this.$nextTick(() => {
            if (this.aside.length > 0) {
              this.$refs.menu.activeIndex = this.active
            }
          })
        },
        immediate: true
      }
    },
    mounted() {
      this.scrollInit()
    },
    beforeDestroy() {
      this.scrollDestroy()
    },
    methods: {
      // 滚动轴初始化
      scrollInit() {
        this.BS = new BScroll(this.$el, {
          mouseWheel: true
          // 如果你愿意可以打开显示滚动条
          // scrollbar: {
          //   fade: true,
          //   interactive: false
          // }
        })
      },
      // 滚动轴销毁
      scrollDestroy() {
        // https://github.com/d2-projects/d2-admin/issues/75
        try {
          this.BS.destroy()
        } catch (e) {
          delete this.BS
          this.BS = null
        }
      },
      handleSideToggle() {
        this.$store.commit('techo/menu/asideCollapseSet', !this.asideCollapse);
      }
    }
  }
</script>